import {
	ApplyShop,
	ApplyUpdateShop
} from 'api/user'
export default {
	data() {
		return {
			name: '',
			intro: '',
			logo: '',
			state: {}
		}
	},
	methods: {
		save(){
			ApplyUpdateShop({
				logo:this.logo,
				title:this.name,
				intro:this.intro
			}).then(res=>{
				if(res.result){
					uni.showToast({
						title:'保存成功'
					})
				}else{
					uni.showToast({
						title:res.msg,
						icon:'none'
					})
				}
			})
		},
		uploadImg() {
			uni.chooseImage({
				count: 1, //默认9
				sizeType: ['original', 'compressed'], //可以指定是原图还是压缩图，默认二者都有
				sourceType: ['album'], //从相册选择
				success: res => {
					uni.uploadFile({
						url: this.state.SRC + '/webapi/School/UploadFile',
						filePath: res.tempFilePaths[0],
						fileType: 'image',
						name: 'file',
						formData: {
							apptoken: this.state.appToken,
							userid: this.state.userId
						},
						success: resdata => {
							let data = JSON.parse(resdata.data)
							if (data.result) {
								this.logo = this.state.SRC + data.fileUrl
							} else {
								this.$msg(data.msg)
							}
						}
					})
				}
			});
		}
	},

	onLoad() {
		this.$store.ready(state => {
			this.state = state
			ApplyShop().then(res => {
				if (res.result) {
					this.name = res.Title
					this.intro = res.Intro
					this.logo = res.Logo
				}
			})
		})

	}
}
